    convert_news_date(y, m, d) {
      const zeroPad = (num, places) => String(num).padStart(places, '0');
      const thisY = new Date(y, m, d).getFullYear();
      const thisM = zeroPad(new Date(y, m, d).getMonth(), 2);
      const thisD = zeroPad(new Date(y, m, d).getDate(), 2);
      return `${thisY}.${thisM}.${thisD}`;
    },
