import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    name: 'Home',
    component: () => import('../views/Home.vue'),
  },
  {
    path: '/front',
    name: 'Front',
    component: () => import('../views/Front.vue'),
    children: [
      {
        path: 'online_store',
        component: () => import('../views/front/Online_store.vue'),
        children: [
          {
            path: ':category',
            component: () => import('../views/front/Category.vue'),
          },
          {
            path: ':category/:id',
            component: () => import('../views/front/Product.vue'),
          },
        ],
      },
      {
        path: 'news',
        component: () => import('../views/front/News.vue'),
      },
      {
        path: 'news_detail/:id',
        component: () => import('../views/front/News_detail.vue'),
      },
      {
        path: 'about',
        component: () => import('../views/front/About.vue'),
      },
      {
        path: 'order_info',
        component: () => import('../views/front/Order_info.vue'),
      },
      {
        path: 'contact',
        component: () => import('../views/front/Contact.vue'),
      },
      {
        path: 'checkout',
        component: () => import('../views/front/Checkout.vue'),
        children: [
          {
            path: 'checkout1',
            component: () => import('../views/front/checkout/Checkout1.vue'),
          },
          {
            path: 'checkout2',
            component: () => import('../views/front/checkout/Checkout2.vue'),
          },
          {
            path: 'checkout3',
            component: () => import('../views/front/checkout/Checkout3.vue'),
          },
          {
            path: 'checkout4',
            component: () => import('../views/front/checkout/Checkout4.vue'),
          },
          {
            path: 'checkout5',
            component: () => import('../views/front/checkout/Checkout5.vue'),
          },
        ],
      },
      {
        path: 'login',
        component: () => import('../views/front/Login.vue'),
      },
    ],
  },
];

const router = new VueRouter({
  routes,
});

export default router;
